//  import { apiPath } from '../../common/js/config.js';
export default {
    name: 'UploadPhoto',
    data() {
        return {
            file:'',  // 
        }
    },
    methods:{
        layer( text ){
            this.$vux.toast.text( text || 'hello', 'middle')
        },
        showLoading(){
            this.$vux.loading.show({
                text: ''
            })
        },
        hideLoading(){
            this.$vux.loading.hide()
        },
        // 
        choosePhoto(event){
            let that = this;
            let target = event.currentTarget;
            this.file = target.files[0];
            // 
            let imgType = /\.jpg|\.png|\.jpeg/i;
            if( !imgType.test( this.file.name ) ){
                return this.layer("");
            }else if((this.file.size/1024000) > 5){
                return this.layer("5M");
            }

            // fileReader 
            if( typeof FileReader == "undefined" ){
                this.layer("");
            }else{
                this.showLoading('...');
                // 
                let reader = new FileReader();
                reader.readAsDataURL( this.file );
                reader.onload = function(){
                    // base64,
                    that.$emit('getImgData',reader.result);
                    // input
                    target.value = '';
                    that.ajaxUploadApi();
                }
            }
        },
        // ajax
        ajaxUploadApi(){
            // 
            let  form = new FormData();
            let  that = this;
            form.append("Filedata",this.file);
            this.$http.post(apiPath.uploadPhoto,form,{
                'headers':{
                    'mimeType':'multipart/form-data'
                },
                progress:function(event){
                    let precent = event.loaded/event.total * 100 + "%";
                    // d
                    that.$emit('progress',precent);
                }
            }).then( res => {
                that.hideLoading();
                if( res.body.code == 1 ){
                    res.body.data.type = 'success';
                    that.$emit('uploadResData',res.body.data);
                }else{
                    that.layer('');
                    that.$emit('uploadResData',{ type : 'fail' });
                }
            }, err => {
                that.hideLoading();
                that.layer('');
                that.$emit('uploadResData',{ type : 'fail' });
            });
        },
    }
}



