
import api from '../../assets/js/api'
import cookie from 'vux/src/tools/cookie/index.js'

export default {
components: {

},
data () {
return {
userInfo:{
"user_id": "",
"username": "",
"nickname": "",
"mobile": "",
"avatar": "",
"identity": "",
"wx_name": ""
},
identity:{
'store':'',
'director':'',
'member': ''
},
account:{
balance: 0,
frost: 0
}
}
},
methods:{
layer( text ){
this.$vux.toast.text( text || 'hello', 'middle')
},
showLoading( text ){
this.$vux.loading.show({
text: text || ''
})
},
hideLoading(){
this.$vux.loading.hide()
},
gotoPage( link ) {
this.$router.push({
path: link
})
},
getUserInfo(){
this.$http.post( api.getUserInfo )
.then( res => {
if( res.code == 1 ){
this.userInfo = res.data.userinfo;
this.account = res.data.account;
}
})
},
logout(){

cookie.remove('token')
this.$router.push({
path: '/login'
})
}
},
mounted() {
this.getUserInfo();
document.getElementsByTagName('title')[0].textContent = '';
}
}



