
import Popup from 'vux/src/components/popup/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Group from 'vux/src/components/group/index.vue'
import Radio from 'vux/src/components/radio/index.vue'
import dateFormat from 'vux/src/tools/date/format.js'

import InfiniteLoading from 'vue-infinite-loading'
import api from '../../assets/js/api'

export default {
components: {
Popup,
Group,
Radio,
InfiniteLoading
},
filters: {
dateFormat
},
directives: {
TransferDom
},
data () {
return {
options1: [
'',
'',
''],
option1:'',
bottomCount: 0,
isShowRadio: false,
year: new Date().getFullYear(),
month: new Date().getMonth() + 1,
accountLog: [],
page: 1,
}
},
compouted:{
type(){
return this.option1 == '' ? 'all' :
this.option1 == '' ? 'income' : 'expend';
}
},
methods:{
layer( text ){
this.$vux.toast.text( text || 'hello', 'middle')
},
showLoading(){
this.$vux.loading.show({
text: ''
})
},
hideLoading(){
this.$vux.loading.hide()
},
showDate(){
const _this = this
this.$vux.datetime.show({
value: this.year + '-' + this.month, //  props
format: 'YYYY-MM',
confirmText:'',
cancelText:'',
minYear: '2018',
maxYear: new Date().getFullYear(),
onHide () {
const _this = this
},
onShow () {
const _this = this
},
onConfirm(val){
_this.year = parseInt(val.split('-')[0]);
_this.month = parseInt(val.split('-')[1]);
_this.accountLog = [];
_this.page = 1;
_this.$nextTick(() => {
_this.$refs.infiniteLoading.$emit('$InfiniteLoading:reset');
});
}
})
},
getListByType(){
this.isShowRadio = false
this.accountLog = [];
this.page = 1;
this.$nextTick(() => {
this.$refs.infiniteLoading.$emit('$InfiniteLoading:reset');
});
},
getList ( $state ) {
this.$http.post( api.accountLog,{
type: this.type,
year: this.year,
month: this.month
}).then( res => {
if( res.code == 1 ){
if( res.data.length ){
this.page++;
this.accountLog = this.accountLog.concat( res.data )
$state.loaded();
}else{
$state.complete();
}
}
} ).catch( e => {
$state.complete();
} )
},
},
mounted() {
document.getElementsByTagName('title')[0].textContent = '';
}
}



