
import XInput from 'vux/src/components/x-input/index.vue'
import Countdown from 'vux/src/components/countdown/index.vue'
import Previewer from 'vux/src/components/previewer/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

import api from '../../assets/js/api'
import UploadPhoto from '../../components/uploadPhoto.vue'
export default {
directives: {
TransferDom
},
components: {
XInput,
Countdown,
UploadPhoto,
Previewer
},
data () {
return {
tjname: '',
name: '',
phone: '',
code: '',
password: '',
rePwdInput: '',
time: 90,
start: true,
isSendCoding: false
}
},
methods:{
layer( text ){
this.$vux.toast.text( text || 'hello', 'middle')
},
showLoading(){
this.$vux.loading.show({
text: ''
})
},
hideLoading(){
this.$vux.loading.hide()
},
finish(){
this.isSendCoding = false;
this.time = 90;
},
sendCode(){
if( !this.$refs.phoneInput.valid ){
this.layer('')
}else{
this.$http.post(api.getCode,{
event: 'register',
mobile: this.phone
}).then( res => {
this.isSendCoding = true;
})
}
},
chooseImg(){
this.$refs['uploadPhoto'].$el.click();
},

getImgDataUrl( value ){


},
uploadProgress(value){


},
uploadResData(value){
if(value.type == 'success'){
let img = new Image();
let that = this;
img.src = value.image_url;

img.onload = function(){

}
}else{

}
},
show (index) {
this.$refs.previewer.show(index)
},
deletePhoto (index){
this.list.splice(index,1);
}
},
mounted() {

}
}



