
import XInput from 'vux/src/components/x-input/index.vue'

import api from '../../assets/js/api'

export default {
components: {
XInput
},
data () {
return {
userInfo:{
"user_id": "",
"username": "",
"nickname": "",
"mobile": "",
"avatar": "",
"identity": "",
"wx_name": ""
},
identity:{
'store':'',
'director':'',
'member': ''
},
account:{
balance: 1000,
frost: 1000
},
money: ''
}
},
methods:{
layer( text ){
this.$vux.toast.text( text || 'hello', 'middle')
},
showLoading(){
this.$vux.loading.show({
text: ''
})
},
hideLoading(){
this.$vux.loading.hide()
},
getUserInfo(){
this.$http.post( api.getUserInfo )
.then( res => {
if( res.code == 1 ){
this.userInfo = res.data.userinfo || {};
this.account = res.data.account || {};
}
})
},
submitCash(){
if( this.money <= 0 || this.money > this.account.balance ){
this.layer('')
}else{
this.showLoading('')
this.$http.post( api.getCash,{
money: this.money
} ).then( res => {
this.hideLoading();
this.$router.push({
path: '/tixiansuc'
})
} ).catch( e => {
this.hideLoading();
} )
}
}
},
mounted() {
this.getUserInfo();
document.getElementsByTagName('title')[0].textContent = '';
}
}



